<template>
    <div class="screen-control">
        <template v-if="mode === 'gallery'">
            <div class="screen-control-button active">
                <PipButton alpha='A' :active="true" />
                <span>VIEW</span>
            </div>
            <div class="screen-control-button active">
                <PipButton alpha='B' :active="true" />
                <span>INFO</span>
            </div>
            <div class="screen-control-button" :class="{'active':canGoPrev}" @click="goPrev">
                <PipButton alpha='Y' :active="canGoPrev" />
                <span>PREV</span>
            </div>
            <div class="screen-control-button" :class="{'active':canGoNext}" @click="goNext">
                <PipButton alpha='X' :active="canGoNext" />
                <span>NEXT</span>
            </div>
        </template>
        <template v-if="mode === 'project'">
            <div class="screen-control-button active" @click="goGallery">
                <PipButton alpha='B' :active="true" />
                <span>BACK</span>
            </div>
            <div class="screen-control-button" :class="{'active':canGoPrevPhoto}" @click="goPrevPhoto">
                <PipButton alpha='Y' :active="canGoPrevPhoto" />
                <span>PREV</span>
            </div>
            <div class="screen-control-button" :class="{'active':canGoNextPhoto}" @click="goNextPhoto">
                <PipButton alpha='X' :active="canGoNextPhoto" />
                <span>NEXT</span>
            </div>
        </template>
    </div>
</template>

<script>
    import PipButton from '@/components/PipButton.vue'
    export default {
        name: 'PipScreenControl',
        components: {
            PipButton
        },
        computed: {
            mode() {
                return this.$store.state.mode
            },
            nowPage() {
                return this.$store.state.galleryPage + 1
            },
            canGoPrev() {
                return this.nowPage > 1
            },
            canGoNext() {
                return this.nowPage < this.$store.state.galleryGroups.length
            },
            nowPhoto(){
                return this.$store.state.projectIndex + 1
            },
            canGoPrevPhoto() {
                return this.nowPhoto > 1
            },
            canGoNextPhoto() {
                return this.nowPhoto < this.$store.state.nowProject.photos.length
            }
        },
        methods: {
            goPrev() {
                if (this.canGoPrev) {
                    this.$store.commit('goPrevGallery')
                }
            },
            goNext() {
                if (this.canGoNext) {
                    this.$store.commit('goNextGallery')
                }
            },
            goPrevPhoto() {
                if (this.canGoPrevPhoto) {
                    this.$store.commit('prevPhoto')
                }
            },
            goNextPhoto() {
                if (this.canGoNextPhoto) {
                    this.$store.commit('nextPhoto')
                }
            },
            goGallery(){
                this.$store.commit('goGallery')
            }
        }
    }
</script>

<style lang="less">
    .screen-control {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        height: 32px;
        margin: 0 8px;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        &-button {
            margin-left: 8px;
            display: flex;
            align-items: center;
            color: #999;
            font-size: .875em;

            &:first-child {
                margin-left: 0;
            }

            &.active {
                color: #eee;
            }
        }
    }
</style>