<template>
    <div class="stack">
        <div class="stack-wrapper">
            <stack-photo class="stack-photo" v-for="p in randomPhotoset" :key="p.codename" :src="p.src.thumb" />
        </div>
        <div class="stack-meta" v-if="title">
            <h2>{{title}}</h2>
        </div>
    </div>
</template>

<script>
    import StackPhoto from '@/components/StackPhoto.vue'
    export default {
        name: 'Stack',
        components: {
            StackPhoto
        },
        props: {
            photoset: Array,
            title: String,
            date: String
        },
        data() {
            return {
                margin1: 0,
                margin2: 0
            }
        },
        computed: {
            randomPhotoset() {
                return this.shuffle(this.photoset)
            }
        },
        methods: {
            shuffle(array) {
                for (let i = array.length - 1; i > 0; i--) {
                    const j = Math.floor(Math.random() * (i + 1));
                    [array[i], array[j]] = [array[j], array[i]];
                }
                return array;
            }
        }
    }
</script>

<style lang="less">
    .stack {
        position: relative;
        width: 102px;
        height: 124px;

        &-wrapper {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            height: 100px;
        }

        &-meta {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            text-align: center;
            white-space: nowrap;

            h2 {
                margin: 0;
                color: #eee;
                font-size: 1em;
                line-height: 20px;
                font-family: 'Brandon Text Medium Italic', 'Futura', 'Futura Md BT', sans-serif;
            }
        }

        &-photo {
            max-width: 68px;
            max-height: 68px;
            display: block;
            border: 2px solid #eee;
            box-shadow: 0 0 1px 2px rgba(0, 0, 0, 0.05), 0 4px 2px 2px rgba(0, 0, 0, 0.025);
            transform-origin: center;
            position: absolute;
            top: 50px;
            left: 50px;
            transition: opacity .5s, transform 1s;
            user-select: none;
            pointer-events: none;
        }
    }
</style>