<template>
    <div>
        <div @click="switchSuperVision">SUPER VISION™</div>
        <div @click="switchScreenshotMode">SCREENSHOT MODE</div>
    </div>
</template>

<script>
    export default {
        name: 'PipControl',
        methods: {
            switchSuperVision() {
                this.$store.commit('switchSuperVision')
            },
            switchScreenshotMode(){
                this.$store.commit('switchScreenshotMode')
            }
        }
    }
</script>