import Vue from 'vue'
import Vuex from 'vuex'
import VueResource from 'vue-resource'

Vue.use(Vuex)
Vue.use(VueResource)

Vue.http.options.root = 'https://camera-dev-cavallo.herokuapp.com/'
Vue.http.options.emulateJSON = true;

export default new Vuex.Store({
    state: {
        timerOff: true,
        cache: 0,
        renderStat: false,
        viewTimes: 0,
        location: '',
        galleryAll: [],
        galleryGroups: [],
        galleryPage: 0,
        mode: 'gallery',
        nowProject: {},
        projectIndex: 0,
        superVision: false,
        screenshotMode: false
    },
    getters: {
        gallery: state => {
            return state.galleryGroups[state.galleryPage]
        }
    },
    mutations: {
        rerender(state) {
            if (state.timerOff) {
                state.renderStat = !state.renderStat
                state.cache = 0
                console.log('Instant render!')
            } else {
                state.cache = state.cache + 1
            }
            let myCache = state.cache
            state.timerOff = false
            setTimeout(() => {
                if (myCache === state.cache) {
                    state.renderStat = !state.renderStat
                    console.log('Debounced render after ' + state.cache + ' blocked!')
                    state.cache = 0
                }
                state.timerOff = true
            }, 500);
        },
        setGallery(state, gallery) {
            state.galleryAll = gallery
            const size = 6
            while (state.galleryAll.length > 0)
                state.galleryGroups.push(state.galleryAll.splice(0, size));
        },
        detVisitor(state) {
            let viewTimes = localStorage.getItem("viewTimes")
            if (!viewTimes) {
                viewTimes = 1
            } else {
                viewTimes++
            }
            localStorage.setItem("viewTimes", viewTimes)
            state.viewTimes = viewTimes
        },
        detLocation(state) {
            let lastLocation = localStorage.getItem("lastLocation")
            if (!lastLocation) {
                lastLocation = "Not Applied."
            }
            state.location = lastLocation
        },
        goPrevGallery(state) {
            state.galleryPage--
        },
        goNextGallery(state) {
            state.galleryPage++
        },
        changeProject(state, payload) {
            state.nowProject = payload
            state.mode = 'project'
        },
        nextPhoto(state) {
            if (state.projectIndex + 1 < state.nowProject.photos.length) {
                state.projectIndex++
            }
        },
        prevPhoto(state) {
            if (state.projectIndex > 0) {
                state.projectIndex--
            }
        },
        resetProjectIndex(state) {
            state.projectIndex = 0
        },
        goGallery(state) {
            state.mode = 'gallery'
        },
        switchSuperVision(state) {
            state.superVision = !state.superVision
        },
        switchScreenshotMode(state) {
            if (state.superVision) {
                state.superVision = false
            }
            state.screenshotMode = !state.screenshotMode
        }
    },
    actions: {
        init(context) {
            context.commit("detVisitor")
            context.commit("detLocation")
            if (process.env.NODE_ENV === 'development') return
            Vue.http.post('newViewer', context.getters.viewerInfo)
        },
        getGallery: function (context) {
            let url = "/json/gallery.json"
            // if (process.env.NODE_ENV === 'development') url = "/json/galerie.json"

            return new Promise((resolve, reject) => {
                Vue.http.get(url).then(response => {
                    const prefix = response.body.prefix
                    let gallery = response.body.gallery.reverse()
                    gallery.forEach(g => {
                        g.cover = []
                        g.photos = []
                        for (let photoIndex = 1; photoIndex <= g.photoIds; photoIndex++) {
                            const photo = {
                                src: {
                                    jpg: prefix + 'c_limit,h_320,q_90:420,w_320/' + g.codeName + "-" + photoIndex + ".jpg",
                                    thumb: prefix + 'c_limit,h_68,q_90:420,w_68/' + g.codeName + "-" + photoIndex + ".jpg"
                                },
                                codeName: g.codeName + "-" + photoIndex
                            }
                            g.photos.push(photo)
                        }
                        for (let coverIndex = 0; coverIndex < g.coverIds.length; coverIndex++) {
                            const coverId = g.coverIds[coverIndex];
                            const cover = g.photos[coverId - 1]
                            g.cover.push(cover)
                            cover.codeName = g.codeName
                            cover.title = g.name
                        }
                    });

                    context.commit("setGallery", gallery)
                    resolve()
                }, response => {
                    console.error("Get gallery failed.")
                    reject()
                });
            })
        },
    }
})