<template>
    <div class="screen">
        <div class="screen-top">
            <div class="screen-title">{{displayTitle}}</div>
            <div class="screen-section">{{displayDesc}}</div>
        </div>
        <div class="screen-main">
            <Gallery v-if="mode === 'gallery'" />
            <Project v-if="mode === 'project'" />
        </div>
        <PipScreenControl />
    </div>
</template>

<script>
    import PipScreenControl from '@/components/PipScreenControl.vue'
    import Gallery from '@/components/Gallery.vue'
    import Project from '@/components/Project.vue'
    export default {
        name: 'PipScreenContent',
        components: {
            Gallery,
            Project,
            PipScreenControl
        },
        computed: {
            mode(){
                return this.$store.state.mode
            },
            displayTitle(){
                if(this.mode === 'gallery'){
                    return 'ワーベルウィンド'
                }else if(this.mode === 'project'){
                    return this.$store.state.nowProject.name
                }
            },
            displayDesc(){
                if(this.mode === 'gallery'){
                    return 'Wirbelwind'
                }else if(this.mode === 'project'){
                    return (this.$store.state.projectIndex + 1) + ' / ' + this.$store.state.nowProject.photos.length
                }
            }
        }
    }
</script>

