<template>
    <div class="pip-screen">
        <div class="page-representation">
            <canvas id="canvas" height="320" width="320"></canvas>
        </div>
        <div class="page-ghost" :class="{'show': superVision, 'through': screenshotMode}">
            <div class="capture-content" id="capture">
                <PipScreenContent />
            </div>
        </div>
        <div class="pip-screen-title">
            <span>ワーベルウィンド</span>
            <span>COLOR</span>
            <span>TOUCH</span>
        </div>
    </div>
</template>

<script>
    import html2canvas from 'html2canvas';
    import Crt from '@/utils/crt_filter.js'
    import CanvasImageData from '@/utils/canvas_image_data.js'

    import PipScreenContent from '@/components/PipScreenContent.vue'

    export default {
        name: 'PipScreen',
        components: {
            PipScreenContent
        },
        computed: {
            renderStat() {
                return this.$store.state.renderStat
            },
            superVision() {
                return this.$store.state.superVision
            },
            screenshotMode() {
                return this.$store.state.screenshotMode
            }
        },
        methods: {
            convertDOM() {
                let ctx = document.getElementById('canvas').getContext('2d');
                html2canvas(document.querySelector("#capture"), {
                    logging: false,
                    useCORS: true,
                    scale: 1,
                    backgroundColor: '#000000'
                }).then(canvas => {
                    ctx.drawImage(canvas, 0, 0);
                    this.renderCRT();
                });
            },
            renderCRT() {
                let ctx = document.getElementById('canvas').getContext('2d');
                let crt_filter = new Crt();
                const height = 320;
                const width = 320;
                let canvas_image_data = new CanvasImageData(ctx.getImageData(0, 0, width, height));
                crt_filter.initialize();
                crt_filter.filter(canvas_image_data, width, height);
                ctx.putImageData(canvas_image_data.imageData, 0, 0);
            }
        },
        mounted() {
            this.convertDOM()
        },
        watch: {
            renderStat() {
                this.convertDOM()
            }
        }
    }
</script>

<style lang="less">
    .pip-screen {
        position: relative;
        width: 336px;
        height: 364px;
        padding: 8px;
        box-sizing: border-box;
        background: linear-gradient(#333, #111);
        border-radius: 8px;

        &-title {
            position: absolute;
            bottom: 8px;
            left: 8px;
            right: 8px;
            height: 20px;
            line-height: 20px;
            text-align: center;
        }
    }

    .page-ghost {
        position: absolute;
        z-index: 2;
        opacity: 0;
        transition: opacity 1s;

        &.show {
            opacity: 1;
        }

        &.through {
            pointer-events: none;
        }

        .capture-content {
            position: absolute;
            top: 0;
            left: 0;
            width: 320px;
            height: 320px;
            overflow: hidden;
        }
    }

    .page-representation {
        position: absolute;
        z-index: 1;
    }
</style>