<template>
    <div class="project">
        <img class="project-photo" :src="photo.src.jpg" :alt="photo.codename" @load="photoReady">
        <div class="project-wrapper"></div>
    </div>
</template>

<script>
    export default {
        name: 'Project',
        computed: {
            project() {
                return this.$store.state.nowProject
            },
            projectIndex() {
                return this.$store.state.projectIndex
            },
            photo() {
                return this.project.photos[this.projectIndex]
            }
        },
        methods: {
            photoReady() {
                this.$store.commit('rerender')
            }
        },
        mounted() {
            this.$store.commit('rerender')
        },
        destroyed() {
            this.$store.commit('resetProjectIndex')
        }
    }
</script>

<style lang="less">
    .project {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        justify-content: center;
        align-items: center;

        &-photo {
            max-height: 252px;
            max-width: 308px;
            display: block;
        }

        &-wrapper {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
        }
    }
</style>