<template>
    <ul class="gallery-list">
        <li class="gallery-list-item" v-for="g in gallery" :key="g.codeName" @click="go(g)">
            <stack :photoset="g.cover" :title="g.name" :date="g.date"></stack>
        </li>
    </ul>
</template>

<script>
    import Stack from '@/components/Stack.vue'

    export default {
        name: 'Gallery',
        components: {
            Stack
        },
        data() {
            return {
                nowProject: {}
            }
        },
        computed: {
            gallery() {
                return this.$store.getters.gallery
            }
        },
        methods: {
            go(g){
                this.$store.commit('changeProject', g)
            }
        }
    }
</script>

<style lang="less">
    .gallery-list {
        list-style: none;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        &-item {
            flex-grow: 1;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
</style>